* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    user-select: none;
    font-family: 'AraHamahHoms';
}

@font-face {
    font-family: 'AraHamahHoms';
    src: url(../fonts/AraHamahHoms/AraHamahHoms-Regular.eot);
    src: url(../fonts/AraHamahHoms/AraHamahHoms-Regular.ttf);
    src: url(../fonts/AraHamahHoms/AraHamahHoms-Regular.woff);
}

body {
    .container {
        width: 650px;
        height: 700px;
        margin: 0 auto;
        position: relative;

        .dreamteam {
            width: 100%;
            height: 100%;
            background-image: url(../images/playground.jpg);
            background-size: 100% 100%;
            background-repeat: no-repeat;

            .select-formation {
                width: 100%;
                height: 20%;
                margin: 0 auto;
                display: flex;
                align-items: center;
                flex-direction: column;
                color: white;

                h1 {
                    font-size: 2em;
                    margin: 10px 0 0;
                }

                h4 {
                    font-size: 1.3em;
                    margin: 10px 0 0;
                }

                .button {
                    margin-top: 10px;
                    padding: 10px;
                    background: black;
                    border: 2px solid #564727;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    direction: rtl;

                    p {
                        margin-left: 10px;
                    }

                    .row {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        margin: 1.5px;

                        span {
                            margin: 1px;
                            background: #FFF;
                            width: 3px;
                            height: 3px;
                        }
                    }
                }

                .button:hover {
                    background: #f0a306;
                    color: black;
                    cursor: pointer;

                    span {
                        background: #000;
                    }
                }
            }

            .playground {
                width: 100%;
                height: 76%;

                .one {
                    display: none;
                }

                .two {
                    display: none;
                }

                .three {
                    display: block;
                }

                .formation {
                    width: 100%;
                    height: 100%;

                    .formation-row {
                        width: 100%;
                        height: 16%;
                        margin-top: 15px;
                        display: flex;
                        justify-content: center;
                        align-items: center;

                        &.defence {
                            .player:first-of-type {
                                margin-top: 30px;
                            }

                            .player:last-of-type {
                                margin-top: 30px;
                            }
                        }

                        .player {
                            width: 17%;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            flex-direction: column;
                            cursor: pointer;
                            transition: all .5s ease-in-out;

                            .player-image {
                                width: 50px;
                                height: 50px;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                border: 3px solid #585d57;
                                border-radius: 50%;
                                position: relative;

                                img {
                                    border-radius: 50%;
                                }

                                .plus {
                                    position: absolute;
                                    top: -5px;
                                    left: -7px;
                                }

                                .wrong {
                                    position: absolute;
                                    top: -5px;
                                    left: -7px;
                                }
                            }

                            .player-name {
                                color: #a9aba9;
                            }
                        }

                        .player:hover {
                            transform: scale(0.9);
                        }
                    }
                }
            }
        }

        .formation-popup {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            background: rgba(27, 29, 30, .85);
            z-index: 5;
            display: none;

            .head {
                width: 100%;
                height: 15%;
                background: rgba(27, 29, 30, .95);
                color: #FFF;
                display: flex;
                justify-content: space-between;
                align-items: center;
                direction: rtl;
                padding: 30px;
                font-size: 1.5em;

                .close {
                    width: 50px;
                    height: 50px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    cursor: pointer;
                }
            }

            .formation-list {
                width: 100%;
                height: 85%;

                .formation-row {
                    width: 100%;
                    height: 100%;
                    display: flex;

                    .formation-data {
                        width: 33%;
                        height: 30%;
                        display: flex;
                        align-items: center;
                        flex-direction: column;
                        padding: 10px;
                        border: 1px solid #36383a;
                        color: #e7e7e7;
                        font-size: 1.5em;
                        cursor: pointer;

                        .plan {
                            width: 100%;
                            height: 50%;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            flex-direction: column;

                            .plan-row {
                                display: flex;
                                justify-content: center;
                                align-items: center;

                                span {
                                    width: 4px;
                                    height: 4px;
                                    margin: 5px;
                                    background: #FFF;
                                }
                            }
                        }

                        .selected-image {
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            display: none;
                        }
                    }

                    .formation-data:hover {
                        .selected-image {
                            display: block;
                        }
                    }
                }
            }
        }

        .players-popup {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            background: rgba(27, 29, 30, .85);
            z-index: 5;
            display: none;

            .head {
                width: 100%;
                height: 15%;
                background: rgba(27, 29, 30, .95);
                color: #FFF;
                display: flex;
                justify-content: space-between;
                align-items: center;
                direction: rtl;
                padding: 30px;
                font-size: 1.5em;

                .close {
                    width: 50px;
                    height: 50px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    cursor: pointer;
                }
            }

            .players-list {
                width: 100%;
                height: 85%;
                overflow: hidden;
                overflow-y: scroll;

                .players-row {
                    list-style: none;
                    display: flex;
                    flex-wrap: wrap;

                    .disabled {
                        opacity: 0.4;
                        pointer-events: none;
                    }

                    .players-data {
                        width: 33%;
                        height: 175px;
                        display: flex;
                        align-items: center;
                        flex-direction: column;
                        padding: 10px;
                        border: 1px solid #36383a;
                        color: #e7e7e7;
                        font-size: 1em;
                        cursor: pointer;

                        .player-image {
                            width: 80px;
                            height: 80px;
                            border-radius: 50%;
                            overflow: hidden;
                            margin: 7px;
                            transition: all .4s ease-in-out;

                            img {
                                width: 100%;
                                height: 100%;
                            }
                        }

                        .player-name {
                            transition: all .4s ease-in-out;
                        }

                        .selected-image {
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            display: none;
                        }
                    }

                    .players-data:hover {
                        .player-name {
                            transform: translateY(-5px);
                            color: #dc9709;
                        }

                        .player-image {
                            transform: translateY(-5px);
                            border: 2px solid #b37d0c;
                            box-shadow: 1px 1px 1px #b37d0c;
                        }

                        .selected-image {
                            display: block;
                        }
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 550px) {
    body {
        .container {
            width: 300px;
            height: 420px;

            .dreamteam {
                .select-formation {
                    height: 19%;

                    h1 {
                        font-size: 15px;
                        margin: 3px 0 0;
                    }

                    h4 {
                        font-size: 15px;
                        margin: 3px 0 0;
                    }

                    .button {
                        font-size: 11px;
                        margin-top: 3px;
                        padding: 3px;

                        .formation {
                            .row {
                                span {
                                    width: 2px;
                                    height: 2px;
                                }
                            }
                        }
                    }
                }

                .playground {
                    height: 70%;

                    .formation {
                        .formation-row {
                            .player {
                                .player-image {
                                    width: 30px;
                                    height: 30px;

                                    img {
                                        width: 85%;
                                    }

                                    .plus {
                                        width: 50%;
                                        top: -2px;
                                        left: -5px;
                                    }

                                    .wrong {
                                        width: 50%;
                                        top: -2px;
                                        left: -5px;
                                    }
                                }

                                .player-name {
                                    font-size: 7px;
                                }
                            }
                        }
                    }
                }
            }

            .formation-popup {
                .head {
                    p {
                        font-size: 18px;
                    }

                    .close {
                        width: 15px;
                        height: 15px;
                    }
                }

                .formation-list {
                    .formation-row {
                        .formation-data {
                            p {
                                font-size: 18px;
                            }

                            .plan {
                                .plan-row {
                                    span {
                                        width: 3px;
                                        height: 3px;
                                        margin: 3px;
                                    }
                                }
                            }

                            .selected-image {
                                img {
                                    width: 15px;
                                }
                            }
                        }
                    }
                }
            }
            .players-popup {
                .head {
                    p {
                        font-size: 18px;
                    }

                    .close {
                        width: 15px;
                        height: 15px;
                    }
                }
                .players-list{
                    .players-row{
                        .players-data {
                            height: 110px;
                            .player-image {
                                width: 40px;
                                height: 40px;
                            }
                            .player-name {
                                font-size: 10px;
                            }
                            .selected-image {
                                img {
                                    width: 15px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media only screen and (min-width: 550px) and (max-width: 900px) {
    body {
        .container {
            width: 550px;
            height: 650px;

            .dreamteam {
                .select-formation {
                    height: 20%;

                    h1 {
                        margin: 3px 0 0;
                    }

                    h4 {
                        margin: 3px 0 0;
                    }

                    .button {
                        margin-top: 3px;
                    }
                }

                .playground {
                    height: 70%;

                    .formation {
                        .formation-row {
                            .player {
                                .player-name {
                                    font-size: 13px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}